// menu
$('.menu-item').click(function () {
    $(this).addClass('active')
        .siblings('.menu-item').not(this)
        .removeClass('active');
    if (this.attributes.scroll) {
        $([document.documentElement, document.body]).animate({
            scrollTop: $(this.attributes.href.value).offset()?.top
        }, 1000);
    }
})


// Sliders
$('.js-slider-container').each((idx, container) => {
    const slider = $(container).find('.slider').slick({
        dots: false,
        arrows: false,
        infinite: true,
        responsive: [
            {
                breakpoint: Infinity,
                settings: {
                    slidesToShow: 4,
                }
            },
            {
                breakpoint: 2000,
                settings: {
                    slidesToShow: 3,
                }
            },
            {
                breakpoint: 1440,
                settings: {
                    slidesToShow: 2,
                }
            },
            {
                breakpoint: 970,
                settings: {
                    slidesToShow: 2,
                }
            }
        ]
    });

    $(container).find('.prev-slide').click(() => slider.slick('slickPrev'))
    $(container).find('.next-slide').click(() => slider.slick('slickNext'))
})

